.education_container {
    height: 650px;
}

.item_container h3 {
    color: var(--color-primary);
}

.item_container p {
    font-family: 'RobotoBold', sans-serif;
    color: var(--color-primary);
}

.path_blur {
    filter: blur(16px);
    position: absolute;
}

.item_container {
    border-radius: 20px;
    border: 3px solid var(--color-primary);
}

.item_container-innowise {
    background-color: #F3695F;
    margin-top: 20px;
    animation: educationContainer 0.5s alternate;
}

.item_container-react {
    background-color: #35848E;
    margin-top: 150px;
    animation: educationContainer 1.5s alternate;
}

.item_container-modsen {
    background-color: #F1B457;
    margin-top: 150px;
    animation: educationContainer 3s alternate;
}

.item_container-univercity {
    background-color: #63CF62;
    margin-top: 150px;
    animation: educationContainer 4s alternate;
}

.container_middle {
    overflow: hidden;
    position: relative;
}

.middle-anim {
    width: 220px;
    height: 550px;
    top: 589px;
    position: absolute;
    background-color: var(--background-primary);
    animation: svgAnim 4s alternate;
}

.education_svg {
    width: 220px;
    height: 400px;
    margin-top: 85px;
}
.education_container-small{
    display: none;
}
@media (max-width: 760px) {
    .education_container{
        display: none;
    }
    .education_container-small{
        display: block;
        position: relative;
        width: 80%;
        height: 540px;
        margin-left: 10%;
        overflow: hidden;
        margin-top: 40px;
    }
    .container_small-animate{
        position: absolute;
        top: 550px;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: var(--background-primary);
        animation: svgAnim 4s alternate;
    }
    .item_container{
        padding: 24px;
        margin-top: 0px;
    }
    .education_svg-small{
        width: 10px;
        height: 40px;
    }
    .item_container p{
        width: 100%;
    }
}

@keyframes svgAnim {
    0% {
        top: 0;
    }

    100% {
        top: 550px;
    }
}



@keyframes educationContainer {
    0% {
        opacity: 0;
    }

    50% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}